import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  Button,
  Popconfirm,
  Table,
  Card,
  Modal,
  Form,
  Input,
  Cascader,
} from "antd";

import {
  fetchAllCentersAction,
  deleteCenterAction,
  updateCenterAction,
} from "../../../redux/actions/centerAction";

import "./style.css";

export default function ListCenters() {
  const columns = [
    {
      title: "Name",
      dataIndex: "name",
      key: "name",
    },
    {
      title: "Address",
      dataIndex: "address",
      key: "address",
    },
    {
      title: "Action",
      dataIndex: "",
      key: "x",
      render: (_, record) => (
        <div style={{ display: "flex", justifyContent: "space-evenly" }}>
          <Popconfirm
            title="Sure to delete?"
            onConfirm={() => dispatch(deleteCenterAction(record._id))}
          >
            <Button danger>Delete</Button>
          </Popconfirm>
          <Button
            type="primary"
            onClick={() => {
              showDataModal(record);
              showModal();
            }}
          >
            Update
          </Button>
        </div>
      ),
    },
  ];

  const dispatch = useDispatch();

  const center = useSelector((state) => state.center);
  useEffect(() => {
    dispatch(fetchAllCentersAction());
  }, [dispatch]);

  const showDataModal = ({ _id, name, address }) => {
    setUpdatedName(name);
    setId(_id);
    setUpdatedAddress(address.split("/"));
  };

  const [visible, setVisible] = React.useState(false);
  const [confirmLoading, setConfirmLoading] = React.useState(false);
  const [updatedName, setUpdatedName] = React.useState("");
  const [updatedAddress, setUpdatedAddress] = React.useState([]);
  const [id, setId] = useState("");

  const showModal = () => {
    setVisible(true);
  };

  const handleOk = async () => {
    setConfirmLoading(true);

    dispatch(
      updateCenterAction(id, {
        name: updatedName,
        address: updatedAddress.join(" "),
      })
    );
    setVisible(false);
    setConfirmLoading(false);
    setUpdatedAddress("");
  };

  const handleCancel = () => {
    setVisible(false);
  };

  return (
    <>
      <Card title="List centers">
        <Table
          columns={columns}
          dataSource={center.centersList}
          bordered
          loading={center.loading}
          pagination={{
            defaultPageSize: 3,
            showSizeChanger: true,
            pageSizeOptions: ["3", "6", "9"],
          }}
        />
        <Modal
          width="50vw"
          centered
          title="Update Center"
          visible={visible}
          onOk={handleOk}
          confirmLoading={confirmLoading}
          onCancel={handleCancel}
        >
          {" "}
          <Form
            style={{ height: "35vh", width: "55vw" }}
            labelCol={{
              span: 4,
            }}
            wrapperCol={{
              span: 14,
            }}
            layout="horizontal"
            initialValues={{
              size: "large",
            }}
            // onValuesChange={onFormLayoutChange}
            size="large"
          >
            <Form.Item label="Center Name" name="name">
              <Input
                required
                defaultValue={updatedName}
                onChange={(e) => setUpdatedName(e.target.value)}
              />
            </Form.Item>

            <Form.Item label="Cascader" name="address">
              <Cascader
                defaultValue={updatedAddress}
                onChange={(e) => setUpdatedAddress(e)}
                options={[
                  {
                    value: "ariana",
                    label: "Ariana",
                    children: [
                      {
                        value: "grande ariana",
                        label: "Grande Ariana",
                      },
                      {
                        value: "petite ariana",
                        label: "Petite Ariana :D",
                      },
                      {
                        value: "sokra",
                        label: "Sokra",
                      },
                    ],
                  },
                ]}
              />
            </Form.Item>
            <div
              style={{
                display: "flex",
                justifyContent: "center",
              }}
            ></div>
          </Form>
        </Modal>
      </Card>

      <br />
    </>
  );
}
