import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Button, Popconfirm, Table, Card, Modal, Form, Input } from "antd";

import "./style.css";

import {
  fetchAllOperators,
  deleteOperator,
  updateOperator,
} from "../../../redux/actions/operatorAction";
export default function ListOperators() {
  const dispatch = useDispatch();
  const operator = useSelector((state) => state.operator);
  useEffect(() => {
    dispatch(fetchAllOperators());
  }, [dispatch]);
  const showDataModal = ({ _id, name, password, tel, cin }) => {
    setUpdatedName(name);
    setUpdatedPassword(password);
    setUpdatedTel(tel);
    setUpdatedCin(cin);
    setId(_id);
  };
  const columns = [
    {
      title: "Name",
      dataIndex: "name",
      key: "name",
    },
    {
      title: "Password",
      dataIndex: "password",
      key: "password",
    },
    {
      title: "Tel",
      dataIndex: "tel",
      key: "tel",
    },
    {
      title: "Cin",
      dataIndex: "cin",
      key: "cin",
    },
    {
      title: "Action",
      dataIndex: "",
      key: "x",
      render: (_, record) => (
        <div style={{ display: "flex", justifyContent: "space-evenly" }}>
          <Popconfirm
            title="Sure to delete?"
            onConfirm={() => dispatch(deleteOperator(record._id))}
          >
            <Button danger>Delete</Button>
          </Popconfirm>
          <Button
            type="primary"
            onClick={() => {
              showDataModal(record);
              showModal();
            }}
          >
            Update
          </Button>
        </div>
      ),
    },
  ];
  const [visible, setVisible] = React.useState(false);
  const [confirmLoading, setConfirmLoading] = React.useState(false);
  const [updatedName, setUpdatedName] = React.useState("");
  const [updatedPassword, setUpdatedPassword] = React.useState("");
  const [updatedTel, setUpdatedTel] = React.useState([]);
  const [updatedCin, setUpdatedCin] = React.useState([]);
  const [id, setId] = useState("");

  const showModal = () => {
    setVisible(true);
  };

  const handleOk = async () => {
    setConfirmLoading(true);

    dispatch(
      updateOperator(id, {
        name: updatedName,
        password: updatedPassword,
        tel: updatedTel,
        cin: updatedCin,
      })
    );
    setVisible(false);
    setConfirmLoading(false);
  };

  const handleCancel = () => {
    setVisible(false);
  };

  return (
    <Card>
      <h1
        style={{
          display: "inline-block",
          fontFamily: "monospace",
          fontWeight: "bold",
          fontSize: 25,
          letterSpacing: 5,
          borderStyle: "solid ",
          borderWidth: 2,
          backgroundColor: "white",
          marginLeft: "23vw",
          marginBottom: "10vh",
          paddingLeft: "2vh",
          paddingRight: "2vh",
        }}
      >
        List Operator
      </h1>

      <br />

      <Table
        columns={columns}
        dataSource={operator.listOperator}
        bordered
        loading={operator.loading}
        pagination={{
          defaultPageSize: 3,
          showSizeChanger: true,
          pageSizeOptions: ["3", "6", "9"],
        }}
      />
      <Modal
        width="50vw"
        centered
        title="Update Operator"
        visible={visible}
        onOk={handleOk}
        confirmLoading={confirmLoading}
        onCancel={handleCancel}
      >
        <Form
          style={{ height: "35vh", width: "55vw" }}
          labelCol={{
            span: 4,
          }}
          wrapperCol={{
            span: 14,
          }}
          layout="horizontal"
          initialValues={{
            size: "large",
          }}
          size="large"
        >
          <Form.Item label="Operator Name" name="name">
            <Input
              required
              defaultValue={updatedName}
              onChange={(e) => setUpdatedName(e.target.value)}
            />
          </Form.Item>
          <Form.Item label="Password" name="password">
            <Input
              required
              defaultValue={updatedPassword}
              onChange={(e) => setUpdatedPassword(e.target.value)}
            />
          </Form.Item>
          <Form.Item label="Tel" name="tel">
            <Input
              required
              defaultValue={updatedTel}
              onChange={(e) => setUpdatedTel(e.target.value)}
            />
          </Form.Item>
          <Form.Item label="Cin" name="cin">
            <Input
              required
              defaultValue={updatedCin}
              onChange={(e) => setUpdatedCin(e.target.value)}
            />
          </Form.Item>

          <div
            style={{
              display: "flex",
              justifyContent: "center",
            }}
          ></div>
        </Form>
      </Modal>
    </Card>
  );
}
