import { Button, Select, Input, Form, Card } from "antd";
import React from "react";
import { useHistory } from "react-router-dom";
import runNotification from "../../../utils/runNotification";
import "./style.css";
import { useDispatch } from "react-redux";
import { addVolunteerAction } from "../../../redux/actions/volunteerAction";

export default function AddVolunteer() {
  const [form] = Form.useForm();
  const history = useHistory();
  const dispatch = useDispatch();
  const onFinish = async (values) => {
    const {
      firstName,
      lastName,
      age,
      phoneNumber,
      cin,
      operator,
      volunteer_team,
    } = values;
    if (!firstName || !lastName || !age || !phoneNumber || !cin)
      runNotification(
        "error",
        "Missing Data",
        "You need to fill the up all the inputs"
      );
    else {
      dispatch(
        addVolunteerAction({
          firstName,
          lastName,
          age,
          phoneNumber,
          cin,
          operator,
          volunteer_team,
        })
      );
      history.push("/dashboard/listVolunteers");
    }
    form.resetFields();
  };

  return (
    <div className="container">
      <Card bordered className="card-form">
        <h1
          style={{
            display: "inline-block",
            fontFamily: "monospace",
            fontWeight: "bold",
            fontSize: 25,
            letterSpacing: 5,
            marginLeft: "23vw",
            marginBottom: "10vh",
            paddingLeft: "2vh",
            paddingRight: "2vh",
          }}
        >
          ADD Volunteer
        </h1>
        <Form
          form={form}
          onFinish={(values) => {
            onFinish(values);
          }}
          style={{
            marginLeft: "12vw",
            height: "40vh",
            width: "50vw",
            overflow: "auto",
          }}
          labelCol={{
            span: 4,
          }}
          wrapperCol={{
            span: 14,
          }}
          layout="horizontal"
          initialValues={{
            size: "large",
          }}
          size="large"
        >
          <Form.Item label=" FirstName" name="firstName">
            <Input required />
          </Form.Item>
          <Form.Item label="LastName" name="lastName">
            <Input required />
          </Form.Item>
          <Form.Item label="Age" name="age">
            <Input required />
          </Form.Item>
          <Form.Item label="PhoneNumber" name="phoneNumber">
            <Input required />
          </Form.Item>
          <Form.Item label="Cin" name="cin">
            <Input required />
          </Form.Item>

          <Form.Item label="Operator" name="operator">
            <Select
              options={[
                {
                  value: "true",
                  label: "true",
                },
                {
                  value: "flase",
                  label: "false",
                },
              ]}
            />
          </Form.Item>
          <Form.Item label="Volunteer Team" name="volunteer_Team">
            <Select
              options={[
                {
                  value: "Tunisian Red Crescent",
                  label: "Tunisian Red Crescent",
                },
                {
                  value: "Tunisian Scouts",
                  label: "Tunisian Scouts",
                },
              ]}
            />
          </Form.Item>
          <div
            style={{
              display: "flex",
              justifyContent: "center",
            }}
          >
            <Button
              style={{ marginRight: "6vw", marginTop: "2vh" }}
              type="primary"
              htmlType="submit"
            >
              Add Volunteer
            </Button>
          </div>
        </Form>
      </Card>
    </div>
  );
}
