import { Button, Input, Form, Card } from "antd";
import { useDispatch } from "react-redux";
import React from "react";
import { useHistory } from "react-router-dom";
import runNotification from "../../../utils/runNotification";
import "./style.css";
import { addOperator } from "../../../redux/actions/operatorAction";

export default function AddOperator() {
  const dispatch = useDispatch();
  const [form] = Form.useForm();
  const history = useHistory();
  const onFinish = async (values) => {
    const { name, password, tel, cin } = values;
    if (!name || !password || !tel || !cin)
      runNotification(
        "error",
        "Missing Data",
        "You need to fill the up all the inputs"
      );
    else {
      dispatch(addOperator({ name, password, tel, cin }));
      history.push("/dashboard/listOperators");
    }
    form.resetFields();
  };
  return (
    <div className="container">
      <Card bordered className="card-form">
        <h1
          style={{
            display: "inline-block",
            fontFamily: "monospace",
            fontWeight: "bold",
            fontSize: 25,
            letterSpacing: 5,
            borderStyle: "solid ",
            borderWidth: 2,
            backgroundColor: "white",
            marginLeft: "23vw",
            marginBottom: "10vh",
            paddingLeft: "2vh",
            paddingRight: "2vh",
          }}
        >
          ADD Operator
        </h1>
        <Form
          form={form}
          onFinish={(values) => {
            onFinish(values);
          }}
          style={{ marginLeft: "12vw", height: "20vh", width: "50vw" }}
          labelCol={{
            span: 4,
          }}
          wrapperCol={{
            span: 14,
          }}
          layout="horizontal"
          initialValues={{
            size: "large",
          }}
          size="large"
        >
          <Form.Item label="Operator Name" name="name">
            <Input required />
          </Form.Item>
          <Form.Item label="Password" name="password">
            <Input required />
          </Form.Item>
          <Form.Item label="Tel" name="tel">
            <Input required />
          </Form.Item>
          <Form.Item label="Cin" name="cin">
            <Input required />
          </Form.Item>

          <div
            style={{
              display: "flex",
              justifyContent: "center",
            }}
          >
            <Button
              style={{ marginRight: "6vw", marginTop: "5vh" }}
              type="primary"
              htmlType="submit"
            >
              Ajouter Operator
            </Button>
          </div>
        </Form>
      </Card>
    </div>
  );
}
