import {
  FETCH_CITIZEN_ERROR,
  FETCH_CITIZEN_LOADING,
  FETCH_CITIZEN_SUCCESS,
  UPDATE_CITIZEN_SUCCESS,
  UPDATE_CITIZEN_ERROR,
} from "../types/citizenTypes";

const initialState = {
  loading: false,
  citizensList: [],
  error: "",
};

function citizenReducer(state = initialState, { type, payload }) {
  switch (type) {
    case FETCH_CITIZEN_SUCCESS:
      return {
        ...state,
        citizensList: payload,
        loading: false,
      };
    case FETCH_CITIZEN_ERROR:
      return {
        ...state,
        error: payload,
        loading: false,
      };
    case FETCH_CITIZEN_LOADING:
      return {
        ...state,
        loading: true,
      };

    case UPDATE_CITIZEN_SUCCESS:
      const updatedCitizen = state.citizensList.findIndex((citizen) => {
        return citizen._id === payload.id;
      });
      const citizen = state.citizensList[updatedCitizen];
      const citizensListUpdated = state.citizensList;
      citizensListUpdated[updatedCitizen] = {
        ...citizen,
        ...payload.citizenData,
      };
      return {
        ...state,
        citizensList: citizensListUpdated,
      };

    case UPDATE_CITIZEN_ERROR:
      return {
        ...state,
        error: payload,
      };

    default:
      return state;
  }
}
export default citizenReducer;
